@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
}

a {
  text-decoration: none;
}

$light-baseColor: #219ebc;
$light-darkColor: #03071e;
$light-button: #0077b6;
$light-indexTop: #e9ecef;
$white-text: #fff;

.navbar {
  position: fixed;
  background: $light-baseColor;
  width: 14.6%;
  height: 100vh;
  text-align: center;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;

  // NavBar Header
  &__header {
    width: 95%;
  }
  &__headerTitle {
    padding-top: 1.4rem;
    padding-bottom: 1.4rem;
    font-size: 1.5rem;
    font-weight: 700;
    color: $white-text;
  }
  &__headerImg {
    border-radius: 50%;
  }
  &__headerText {
    margin: auto;
    width: 95%;
    padding-top: 1.4rem;
    color: $white-text;
    font-size: .9rem;
    text-align: justify;
  }
  &__headerSpan {
    display: flex;
    margin: auto;
    margin-top: 4.4rem;
    margin-bottom: 4.4rem;
    width: 80%;
    height: 1px;
    background: $white-text;
  }

  // NavBar body
  &__body {
    width: 100%;
  }
  &__bodyLinkBox {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
  }
  &__bodyLink {
    font-size: 1rem;
    font-weight: 700;
    margin-bottom: .7rem;
    color: $white-text;
    transition: all 200ms ease-in-out;
    &:hover {
      color: $light-darkColor;
      cursor: pointer;
    }
  }
  &__bodyButton {
    margin-top: 1rem;
    margin-bottom: 1rem;
    width: 40%;
    height: 2rem;
    border-radius: 5px;
    border: none;
    background: $light-button;
    color: $white-text;
    font-size: 1rem;
    font-weight: 600;
    box-shadow: darken($light-button, 10%) 1px 2px 2px;
    transition: all 200ms ease-in-out;
    &:hover {
      cursor: pointer;
      transform: scale(1.1);
      background: lighten($light-button, 5%);
    }
    &:active {
      box-shadow: none;
      box-shadow: inset darken($light-button, 10%) 1px 2px 2px;
    }
  }

    // NavBar DarkMode
    &__darkmode {
      padding-top: 2rem;
    }
    &__darkmodeText {
      font-size: .8rem;
      font-weight: 600;
      color: $white-text;
    }
}

  // Toggle DarkMode
  .navbar__darkmodeButton {
    width: 0;
    height: 0;
    visibility: hidden;
  }
  .navbar__darkmodeLabel {
    display: block;
    width: 6rem;
    height: 2rem;
    background-color: lighten($light-button, 5%);
    border-radius: 100px;
    position: relative;
    cursor: pointer;
    transition: 0.5s;
    box-shadow: inset darken($light-button, 20%) 1px 2px 2px;
  }
  .navbar__darkmodeLabel::after {
    content: "";
    width: 1rem;
    height: 1rem;
    background-color: $white-text;
    position: absolute;
    border-radius: 70px;
    top: 8px;
    left: 8px;
    transition: 0.5s;
    box-shadow: darken($light-button, 20%) 2px 3px 3px;
  }
  .navbar__darkmodeButton:checked + .navbar__darkmodeLabel:after {
    left: calc(100% - 7px);
    transform: translateX(-100%);
  }
  .navbar__darkmodeButton:checked + .navbar__darkmodeLabel {
      background-color: darken($light-button, 10%);
    }
  .navbar__darkmodeLabel:active::after {
    width: 1.4rem;
  }

  // INDEX
  .index {
    width: 85.4%;
    height: 100vh;
    margin-left: auto;
    
    // Index top
    &__header {
      background: lighten($light-indexTop, 5%);
      padding-top: 4rem;
      padding-left: 10rem;
      padding-bottom: 4.9rem;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      border-bottom: darken($light-indexTop, 20%) solid 1px;
    }
    &__headerLeft {
      width: 60%;
      margin-right: 5rem;
    }
    &__headerLeft_title {
      font-size: 3rem;
      padding-bottom: .6rem;
    }
    &__headerLeft_subtitle {
      font-weight: 400;
      font-size: 1.3rem;
      color: darken($white-text, 70%);
      margin-bottom: 1rem;
    }
    &__headerLeft_description {
      text-align: left;
    }
    &__headerLeft_button {
      margin-top: 3rem;
      height: 3rem;
      width: 10rem;
      border-radius: 5px;
      border: none;
      background: $light-button;
      color: $white-text;
      font-size: .8rem;
      font-weight: 600;
      box-shadow: darken($light-button, 10%) 1px 2px 2px;
      transition: all 200ms ease-in-out;
      &:hover {
        cursor: pointer;
        background: lighten($light-button, 5%);
      }
      &:active {
        box-shadow: none;
        box-shadow: inset darken($light-button, 10%) 1px 2px 2px;
      }
    }

    // Index Body
    &__body {
      padding-left: 10rem;
      padding-top: 4rem;
      width: 70%;
    }
    &__body_title {
      font-size: 2.3rem;
      margin-bottom: 1.4rem;
    }
    &__body_titleSpan {
      height: 2rem;
      width: .3rem;
      display: inline-block;
      background: $light-button ;
      margin-right: .4rem;
    }
    &__body_text {
      color: darken($white-text, 80%);
      margin-bottom: 2.5rem;
    }
    &__body_tasksBox {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
      width: 100%;
      height: auto;
      margin-bottom: 2.8rem;
    }
    &__body_task {
      width: 27%;
      padding-left: 1.4rem;
      padding-right: 1.4rem;
      padding-bottom: 1.4rem;
      border-radius: 10px;
      border: 2px solid $light-darkColor;
      transition: all 200ms ease-in-out;
    }
    &__body_taskTitle {
      width: 100%;
      padding-top: 1.2rem;
      margin-bottom: .3rem;
      font-weight: 800;
    }
    &__body_taskText {
      text-align: left;
      font-weight: 600;
    }
    &__body_button {
      height: 3rem;
      width: 10rem;
      border-radius: 5px;
      border: none;
      background: $light-button;
      color: $white-text;
      font-size: .8rem;
      font-weight: 600;
      box-shadow: darken($light-button, 10%) 1px 2px 2px;
      transition: all 200ms ease-in-out;
      &:hover {
        cursor: pointer;
        background: lighten($light-button, 5%);
      }
      &:active {
        box-shadow: none;
        box-shadow: inset darken($light-button, 10%) 1px 2px 2px;
      }
    }
  }

  // Animate taskboxes
  #taskOne:hover {
    background: $light-baseColor;
    color: $white-text;
    transform: scale(1.1);
  }
  #taskTwo:hover {
    background: $light-baseColor;
    color: $white-text;
    transform: scale(1.1);
  }
  #taskThree:hover {
    background: $light-baseColor;
    color: $white-text;
    transform: scale(1.1);
  }

  // Skill part
  .skills {
    width: 85.4%;
    height: auto;
    margin-left: auto;
    text-align: center;
    &__title {
      padding-top: 4rem;
      font-size: 1.8rem;
    }
    &__button {
      margin-top: 1rem;
      margin-bottom: 2rem;
      height: 3rem;
      width: 10rem;
      border-radius: 5px;
      border: none;
      background: $light-button;
      color: $white-text;
      font-size: .8rem;
      font-weight: 600;
      box-shadow: darken($light-button, 10%) 1px 2px 2px;
      transition: all 200ms ease-in-out;
      &:hover {
        cursor: pointer;
        background: lighten($light-button, 5%);
      }
      &:active {
        box-shadow: none;
        box-shadow: inset darken($light-button, 10%) 1px 2px 2px;
      }
    }
    &__span {
      width: 80%;
      height: 1px;
      display: block;
      background: darken($white-text, 20%);
      margin-left: auto;
      margin-right: auto;
    }
    
    // CV page 
    &__box {
      margin-left: auto;
      margin-right: auto;
      width: 100%;
      padding-top: 2rem;
      padding-bottom: 5rem;
      background: $light-indexTop;
    }
    &__cv {
      width: 60%;
      margin-left: auto;
      margin-right: auto;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      background: $white-text;
      padding-bottom: 1.8rem;
      box-shadow: darken($light-button, 60%) 2px 1px 25px;

    }

    // CV Header
    &__cvHeader {
      width: 100%;
      background: $white-text;
    }
    &__cvHeader_title {
      padding-top: 2.8rem;
      padding-left: 2.8rem;
      text-align: left;
      font-size: 2.3rem;
      font-weight: 800;
      color: $light-baseColor;
      text-shadow: #333 1px 1px 1px;
    }
    &__cvHeader_subtitle {
      margin-top: 1rem;
      padding-left: 2.8rem;
      text-align: left;
      font-size: 1.3rem;
      color: darken($white-text, 60%);
      margin-bottom: 1.4rem;
    }
    &__cvHeader_box {
      display: flex;
      width: 90%;
      margin-left: auto;
      margin-right: auto;
      margin-top: 1rem;
      margin-bottom: 1rem;
    }
    &__cvHeader_img {
      border-radius: 50%;
      width: auto;
    }
    &__cvHeader_description {
      width: 81%;
      margin-top: auto;
      margin-bottom: auto;
      text-align: justify;
      padding-left: 2rem;
    }

    // CV Body
    &__cvBody {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      flex-wrap: wrap;
      text-align: left;
      width: 95%;
      margin-left: auto;
      margin-right: auto;
      padding-top: 1rem;
    }
    &__cvBody_left {
      width: 40%;
    }
    &__cvBody_leftTitle {
      margin-bottom: 1rem;
      color: $light-baseColor;
    }
    &__skillName {
      margin-bottom: .3rem;
    }
    &__skillDate {
      color: darken($white-text, 60%);
    }
    &__skillSociete {
      color: darken($white-text, 60%);
    }
    &__skillAdresse {
      color: darken($white-text, 60%);
    }
    &__skillCity {
      color: darken($white-text, 60%);
      margin-bottom: 1rem;
    }
    
    &__cvBody_right {
      width: 50%;
    }
    &__cvBody_rightTitle {
      margin-bottom: 1rem;
      color: $light-baseColor;
    }
    &__diplomesName {
      margin-bottom: .3rem;
    }
    &__diplomesSchool {
      color: darken($white-text, 60%);
    }
    &__diplomesCity {
      color: darken($white-text, 60%);
      margin-bottom: .3rem;
    }
    &__permis {
      display: flex;
    }
  }

  #skills__spec {
    margin-top: .8rem;
    margin-bottom: .8rem;
  }
  #skills__permis {
    margin-top: .6rem;
    margin-bottom: .3rem;
  }
  #skills__interest {
    margin-top: .8rem;
    margin-bottom: .8rem;
  }

  // Index Contact

  .contactBox {
    height: 100vh;
    text-align: center;
  }

  .contact {
    width: 85.4%;
    height: 10vh;
    margin-left: auto;
    text-align: center;
    padding-top: 3rem;
    padding-bottom: 4rem;
    background: $light-indexTop;
    &__div {
      width: 80%;
      margin: auto;
    }
    &__divTitle {
      margin-bottom: 1rem;
      font-size: 2.1rem;
    }
    &__divText {
      text-align: justify;
    }
    &__divTextAlt {
      margin-top: 1rem;
      font-size: 1.6rem;
      font-weight: 500;
    }
  }

  .contactForm {
    width: 85.4%;
    height: auto;
    margin-left: auto;
    &__title {
      margin-top: 2rem;
      margin-bottom: 4rem;
    }
    &__div {
      width: 80%;
      margin-left: auto;
      margin-right: auto;
    }
    &__form {
      width: 100%;
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      align-items: center;
      padding-top: 2rem;
      padding-bottom: 2rem;
    }
    &__inputBox {
      width: 100%;
      display: flex;
      flex-direction: row;
      justify-content: center;
    }
    &__input {
      margin-left: 4rem;
      margin-right: 4rem;
      margin-bottom: 2rem;
      width: 24.65%;
      height: 2.5rem;
      overflow: hidden;
      padding-left: 1rem;
      font-size: 1.2rem;
      border-radius: 8px;
      border: 1px solid;
    }
    &__textArea {
      width: 60.5%;
      padding-left: 1rem;
      padding-top: 1rem;
      font-size: 1.2rem;
      border-radius: 8px;
      border: 1px solid;
    }
    &__button {
      margin-top: 3rem;
      margin-right: auto;
      margin-left: 15.5rem;;
      height: 3rem;
      width: 10rem;
      border-radius: 5px;
      border: none;
      background: $light-button;
      color: $white-text;
      font-size: .8rem;
      font-weight: 600;
      box-shadow: darken($light-button, 10%) 1px 2px 2px;
      transition: all 200ms ease-in-out;
      &:hover {
        cursor: pointer;
        background: lighten($light-button, 5%);
      }
      &:active {
        box-shadow: none;
        box-shadow: inset darken($light-button, 10%) 1px 2px 2px;
      }
    }
  }